import * as componentI18n from "dojo/i18n!../nls/resources";

import { Component, H, Pojo } from "../../Component";
import TextOptionDropdown from "../../Dropdowns/TextOptionDropdown";
import SvgButton from "../../Buttons/SvgButton";
import LoaderIcon from "../../Loaders/LoaderIcon";

export interface CustomAction {
    allowed: (item: Pojo) => boolean;
    asynchronous: boolean;
    onAction: (item: Pojo) => dojo.Deferred<any>;
    name: string;
    icon?: string;
}

export interface MiniCardRowProps {
    /**
     * Unique key for the component.
     * @type {string}
     */
    key: string;

    /**
     * The item for the card.
     * @type {object}
     */
    item: Pojo;

    /**
     * Mapping of item ids currently in the loading state.
     * @type {object}
     */
    loadingItems: { [itemId: string]: Pojo };

    /**
     * Mapping of item ids currently in the selected state.
     * @type {object}
     */
    selectedItems: { [itemId: string]: Pojo };

    /**
     * The current sort field.
     * @type {string}
     */
    sortField: string;

    /**
     * The item currently being viewed in the details panel (if available)
     * @type {object}
     */
    activeItem: Pojo | undefined;

    /**
     * The portal that the item is associated with.
     * @type {object}
     */
    portal: any;

    /**
     * Handler for viewing the item's details.
     * @type {function}
     */
    onShowItemDetails: (item: Pojo) => void;

    /**
     * Handler for selecting the item.
     * @type {function}
     */
    onSelectionClick: (item: Pojo) => void;

    /**
     * Array of custom actions available on the item.
     * @type {array}
     */
    customActions: CustomAction[];

    /**
     * Handler for selecting a custom action.
     * @type {function}
     */
    onCustomActionClick: (actionTitle: string, item: Pojo) => void;

    /**
     * If true, the primary action will not be available from the card.
     * @type {boolean}
     */
    disableMainAction?: boolean;

    /**
     * If true, custom actions without an icon will not be shown on the card.
     * @type {boolean}
     */
    hideEllipses?: boolean;
}

/**
 * A small, row-like item card.
 */
export default class MiniCardRow extends Component<MiniCardRowProps> {
    constructor(props: MiniCardRowProps) {
        super(props);

        this.handleShowItemDetails = this.handleShowItemDetails.bind(this);
        this.handleSelectionClick = this.handleSelectionClick.bind(this);
        this.handleIconActionClick = this.handleIconActionClick.bind(this);
        this.handleCustomActionClick = this.handleCustomActionClick.bind(this);
    }

    public render(tsx: H) {
        const selected = !!this.props.selectedItems[this.props.item.id];
        const loading = !!this.props.loadingItems[this.props.item.id];
        const active = this.props.activeItem && this.props.activeItem.id === this.props.item.id;

        const availableActions = this.props.customActions.filter((action: CustomAction) => {
            return action.allowed(this.props.item);
        }).map((action: CustomAction) => ({ value: action.name, text: action.name, icon: action.icon }));

        const availableIconActions = availableActions
            .filter((action: { value: string, text: string, icon?: string }) => {
                return !!action.icon;
            });

        const availableMiscActions = availableActions
            .filter((action: { value: string, text: string, icon?: string }) => {
                return !action.icon;
            });

        let selectButton;
        if (!this.props.disableMainAction) {
            if (loading) {
                selectButton = <LoaderIcon />;
            } else {
                selectButton = (
                    <SvgButton
                        focusable={true}
                        handleClick={this.handleSelectionClick}
                        key={`${this.props.item.id}-selection-btn`}
                        title={selected ? componentI18n.remove : componentI18n.add}
                    >
                        {
                            selected ? (
                                <svg
                                    width="16px"
                                    height="16px"
                                    viewBox="0 0 16 16"
                                >
                                    <g stroke="none" stroke-width="1">
                                        <g id="minus-circled-1px-16">
                                            <g>
                                                <polygon points="3 7 12 7 12 8 3 8" />
                                                <path
                                                    d="M7.501,0 C3.365,0 0,3.364 0,7.5 C0,11.636 3.365,15 7.501,15 C11.636,15 15,11.636 15,7.5 C15,3.364 11.636,0 7.501,0 Z M7.501,14 C3.916,14 1,11.084 1,7.5 C1,3.916 3.916,1 7.501,1 C11.085,1 14,3.916 14,7.5 C14,11.084 11.085,14 7.501,14 Z"
                                                />
                                            </g>
                                        </g>
                                    </g>
                                </svg>
                            ) : (
                                <svg
                                    width="16px"
                                    height="16px"
                                    viewBox="0 0 16 16"
                                >
                                    <g stroke="none" stroke-width="1">
                                        <g id="plus-circled-1px-16">
                                            <g>
                                                <polygon
                                                    points="12 7 7.999 7 8 3 7.001 3 7 7 2.999 7 2.999 8 7 8 6.999 12 7.998 12 7.999 8 12 8"
                                                />
                                                <path
                                                    d="M7.501,0 C3.365,0 0,3.364 0,7.5 C0,11.636 3.365,15 7.501,15 C11.636,15 15,11.636 15,7.5 C15,3.364 11.636,0 7.501,0 Z M7.501,14 C3.916,14 1,11.084 1,7.5 C1,3.916 3.916,1 7.501,1 C11.085,1 14,3.916 14,7.5 C14,11.084 11.085,14 7.501,14 Z"
                                                />
                                            </g>
                                        </g>
                                    </g>
                                </svg>
                            )
                        }
                    </SvgButton>
                );
            }
        }

        const iconActionBtns = availableIconActions.map((action: { value: string, text: string, icon: string }) => (
            <SvgButton
                focusable={true}
                handleClick={this.handleIconActionClick}
                key={`${this.props.item.id}-${action.text}`}
                title={action.text}
                value={action.value}
            >
                <div class="card-mcr__custom-icon-container" innerHTML={action.icon} />
            </SvgButton>
        ));
    
        const containerClasses = {
            "card-mcr__container": true,
            "card-mcr__container--selected": selected,
            "card-mcr__container--active": active
        };
    
        return (
            <div
                key={this.props.item.id}
                classes={containerClasses}
                onclick={this.handleShowItemDetails}
            >
                <img
                    src={this.props.item.iconURI}
                    class="card-mcr__item-icon"
                    title={this.props.item.displayName}
                    alt=""
                />
                <button
                    id={`item-${this.props.item.id}-card-title`}
                    class="card-mcr__item-title"
                    title={active ? componentI18n.hideItem : componentI18n.viewItem}
                    tabindex="0"
                >
                    {this.props.item.title}
                </button>
                <div class="card-mcr__spacer" />
                <div class="card-mcr__action-container">
                    {selectButton}
                    {iconActionBtns}
                    {
                        availableMiscActions.length > 0 && !this.props.hideEllipses ?
                        <TextOptionDropdown
                            key={`${this.props.item.id}-actions`}
                            options={availableMiscActions}
                            onSelect={this.handleCustomActionClick}
                            selectedOptionValue={undefined}
                            justify="right"
                        >
                            <SvgButton
                                key={`${this.props.item.id}-action-btn`}
                                focusable={false}
                                title={componentI18n.actions}
                            >
                                <svg
                                    width="16px"
                                    height="16px"
                                    viewBox="0 0 16 16"
                                    style="transform: translate(0, -2px);"
                                >
                                    <g stroke="none" stroke-width="1">
                                        <g id="ellipses-1px-16">
                                            <path
                                                d="M1,7 L3,7 L3,9 L1,9 L1,7 Z M7,7 L9,7 L9,9 L7,9 L7,7 Z M13,7 L15,7 L15,9 L13,9 L13,7 Z"
                                            />
                                        </g>
                                    </g>
                                </svg>
                            </SvgButton>
                        </TextOptionDropdown> : null
                    }
                </div>
            </div>
        );
    }

    private handleShowItemDetails(e: Event) {
        this.props.onShowItemDetails(this.props.item);
    }

    private handleSelectionClick(e: any) {
        e.stopPropagation();
        this.props.onSelectionClick(this.props.item);
    }

    private handleIconActionClick(e: any) {
        e.stopPropagation();
        if (e.target.value) {
            this.props.onCustomActionClick(e.target.value, this.props.item);
        }
    }

    private handleCustomActionClick(actionTitle: string) {
        this.props.onCustomActionClick(actionTitle, this.props.item);
    }
}
